import utils from "common/utils.js"

import date from "./child/date.vue"
import free from "./child/free.vue"
export default {
	data() {
		return {
			bottom:0,
			iphonex:false,
			appointment:0,
			isClose:0,
		}
	},
	onLoad() {

	},
	components:{
		"v-date":date,
		free
	},
	onShow(){
		this.bottom = utils.bottom	
		this.iphonex = utils.iphonex
		this.appointment =  this.bottom + 100;
	},
	methods: {
		openAdd(){
			this.isClose = 1;
		},
		closeRefuse(){
			this.isClose = 0;
		}
	}
}
